import React, { Component } from 'react';
import Webcam from "react-webcam";
import Tesseract /*PSM, OEM, { createWorker, createScheduler }*/ from 'tesseract.js';
// import { OCRAD } from 'ocrad.js';
import './App.css';
import {
  Row,
  Col,
  Container,
  Nav,
  Navbar,
  NavDropdown,
  Modal,
  Button
} from 'react-bootstrap';
import logo from './Adria-logo.png';
import engbw from './eng_bw.png';

const ocrad = require('async-ocrad');

